div.emojiPicker-3m1S-j {
  border: 1px solid transparent;
  border-radius: 0;
  
  div.theme-dark &, div.theme-light & {
    background-color: var(--fd-theme-current-popout);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1), 0 1px 8px rgba(0, 0, 0, 0.2);
    border-color: var(--fd-theme-current-popout-outline);
  }
}

div.emojiPicker-3m1S-j div.header-1nkwgG {
  padding: 12px;
  border-bottom: 0;

  div.searchBar-2pWH0_.searchBarLight-1NxoDG {
    padding: 0;
    border-radius: 0;

    div.theme-dark &, div.theme-light & {
      background-color: transparent;
    }

    div.searchBarInner-1_Tg2R {
      padding: 0;
      border-radius: 0;
      border: 2px solid transparent;
      border-radius: 0 !important;
  
      div.theme-dark &, div.theme-light & {
        background-color: transparent;
        border-color: var(--fd-theme-current-outline);
      }
      &:hover {
        div.theme-dark &, div.theme-light & {
          border-color: var(--fd-theme-current-outline-alt);
        }
      }
      &:focus-within {
        div.theme-dark &, div.theme-light & {
          background-color: var(--fd-theme-light-bg);
          border-color: var(--int-accent-color);
        }

        div.searchBarIcon-1J6sKG i.icon-3cZ1F_ {
          filter: brightness(0);
        }
      }
    }

    input {
      font-size: 14px;
      font-weight: normal;
      line-height: 28px;
      height: 28px;
      padding: 0 10px;

      div.platform-linux ~ div & {
        letter-spacing: -0.4px;
      }

      div.theme-dark &, div.theme-light & {
        color: var(--fd-theme-current-invert);
      }
      &:focus {
        div.theme-dark &, div.theme-light & {
          color: var(--fd-theme-dark-bg);
        }
      }

      &::placeholder {
        line-height: 28px;
        
        div.theme-dark &, div.theme-light & {
          color: var(--fd-theme-current-blurb);
        }
      }
      &:focus::placeholder {
        div.theme-dark &, div.theme-light & {
          color: var(--fd-theme-light-blurb);
        }
      }
    }

    div.searchBarIcon-1J6sKG {
      width: 28px;
      margin: 0;

      i.icon-3cZ1F_ {
        background-size: 50%;
        background-position: center;
        background-repeat: no-repeat;
        opacity: 0;
        transform: none;
        transition: none;
        height: 100%;
        width: 100%;

        &.visible-3V0mGj {
          opacity: 1;
        }

        div.theme-light & {
          filter: brightness(0);
        }

        &.iconSearchBarEyeGlass-2n38zb {
          @include fluent-icon-bg('search_white');
        }
        &.iconSearchBarClear-2DW-Kr {
          @include fluent-icon-bg('delete_white');
          cursor: default;

          &:hover {
            div.theme-dark &, div.theme-light & {
              background-color: var(--fd-theme-dark-hover);
            }
          }
          &:active {
            div.theme-dark &, div.theme-light & {
              background-color: var(--fd-theme-dark-active);
            }
          }
        }
      }
    }
  }
}
