@font-face {
  font-family: 'Meiryo';
  src: local('Meiryo'),
    url('https://intrnl.github.io/fluentdiscord/src/assets/fonts/Meiryo.woff2') format('woff2'),
    url('https://cdn.staticaly.com/gh/intrnl/fluentdiscord/master/assets/fonts/Meiryo.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
  unicode-range: U+3000303F, U+3040309F, U+30A030FF, U+FF00-FFEF, U+4E00-9FAF;
}

@font-face {
  font-family: 'Meiryo';
  src: local('Meiryo Italic'), local('Meiryo-Italic'),
    url('https://intrnl.github.io/fluentdiscord/src/assets/fonts/Meiryo-Italic.woff2') format('woff2'),
    url('https://cdn.staticaly.com/gh/intrnl/fluentdiscord/master/assets/fonts/Meiryo-Italic.woff2') format('woff2');
  font-weight: normal;
  font-style: italic;
  unicode-range: U+3000303F, U+3040309F, U+30A030FF, U+FF00-FFEF, U+4E00-9FAF;
}

@font-face {
  font-family: 'Meiryo';
  src: local('Meiryo Bold'), local('Meiryo-Bold'),
    url('https://intrnl.github.io/fluentdiscord/src/assets/fonts/Meiryo-Bold.woff2') format('woff2'),
    url('https://cdn.staticaly.com/gh/intrnl/fluentdiscord/master/assets/fonts/Meiryo-Bold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;
  unicode-range: U+3000303F, U+3040309F, U+30A030FF, U+FF00-FFEF, U+4E00-9FAF;
}

@font-face {
  font-family: 'Meiryo';
  src: local('Meiryo Bold Italic'), local('Meiryo-BoldItalic'),
    url('https://intrnl.github.io/fluentdiscord/src/assets/fonts/Meiryo-BoldItalic.woff2') format('woff2'),
    url('https://cdn.staticaly.com/gh/intrnl/fluentdiscord/master/assets/fonts/Meiryo-BoldItalic.woff2') format('woff2');
  font-weight: bold;
  font-style: italic;
  unicode-range: U+3000303F, U+3040309F, U+30A030FF, U+FF00-FFEF, U+4E00-9FAF;
}
