div.standardSidebarView-3F1I7i {
  div.theme-dark &, div.theme-light & {
    background-color: var(--fd-theme-current-bg);
  }

  div.platform-win & {
    top: 0;
  }
}

div.sidebarRegion-VFTUkN {
  flex: 0 0;
  z-index: 3;

  div.theme-dark &, div.theme-light & {
    background-color: transparent;
  }

  &::before {
    background-image: var(--int-bg-image);
    background-color: var(--fd-theme-current-bg);
    background-attachment: fixed;
    transform: scale(1.1);
    filter: brightness(var(--int-bg-opacity)) blur(10px) saturate(150%);
    pointer-events: none;
    content: '';
    width: 100vw;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: -1;

    div.platform-win & {
      height: auto;
      top: -($titlebar-height);
    }
  }
  &::after {
    @include fluent-asset('noise.png');
    mix-blend-mode: exclusion;
    pointer-events: none;
    content: '';
    height: 100%;
    width: calc(100% + 20px);
    position: absolute;
    z-index: -1;

    div.platform-win & {
      height: auto;
      top: -($titlebar-height);
      bottom: 0;
    }
  }

  div.scrollerWrap-2lJEkd {
    display: flex;
    flex-direction: column;
  
    &::before {
      font-size: 16px;
      flex-shrink: 0;
      flex-grow: 0;
      display: flex;
      align-items: center;
      height: 48px;
      padding: 0 12px;

      div.platform-linux & {
        letter-spacing: -0.4px;
      }
      div.platform-win & {
        margin-top: -($titlebar-height);
        padding-top: $titlebar-height;
      }
  
      div.theme-dark &, div.theme-light & {
        background-color: var(--fd-theme-current-fluent);
        color: var(--fd-theme-current-invert);
      }
    }
  
    [aria-label='USER_SETTINGS'] &::before {
      content: 'Settings';
    }
    [aria-label='GUILD_SETTINGS'] &::before {
      content: 'Guild Settings';
    }
    [aria-label='RTC_DEBUG'] &::before {
      content: 'RTC Debug';
    }

    div.platform-win & {
      padding-top: $titlebar-height;
    }
  }

  div.scroller-2FKFPG {
    flex-grow: 1;
    flex-shrink: 1;

    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-fluent);
    }
  }
}
div.sidebar-CFHs9e {
  width: 260px;
  padding: 0;
}

div.side-8zPYf6 {
  div.header-2RyJ0Y {
    font-size: 14px;
    font-weight: normal;
    line-height: normal;
    text-transform: none;
    display: flex;
    align-items: center;
    height: 48px;
    margin-top: 0;
    padding: 0 12px;

    div.platform-linux & {
      letter-spacing: -0.4px;
    }
  
    div.theme-dark &, div.theme-light & {
      color: var(--fd-theme-current-blurb);
    }

    &:first-child {
      margin-top: 0;
    }
  }
  div.item-PXvHYJ {
    cursor: default;
    display: flex;
    align-items: center;
    height: 48px;
    margin: 0;
    padding: 0 12px 0 (12px + 5px);
    border-radius: 0;
    position: relative;

    div.platform-linux & {
      letter-spacing: -0.4px;
    }

    div.theme-dark &, div.theme-light & {
      color: var(--fd-theme-current-invert) !important;
    }

    &.itemSelected-1qLhcL {
      background-color: transparent !important;

      &::after {
        background-color: var(--int-accent-color);
        height: 50%;
      }
    }
    &:hover {
      div.theme-dark &, div.theme-light & {
        background-color: var(--fd-theme-current-hover) !important;
      }
    }
    &:active {
      div.theme-dark &, div.theme-light & {
        background-color: var(--fd-theme-current-active) !important;
      }
    }

    &::before {
      background-size: 21px;
      background-position: center;
      background-repeat: no-repeat;
      content: '';
      display: none;
      height: 21px;
      width: 21px;
      margin-right: 12px;

      div.theme-light & {
        filter: brightness(0);
      }
    }

    &::after {
      transform: translateY(-50%);
      content: '';
      height: 0;
      width: 5px;
      position: absolute;
      top: 50%;
      left: 0;
      z-index: 3;
    }
  }
  div.separator-gCa7yv {
    height: auto;
    margin: 16px 0 0 0;
    
    div.theme-dark &, div.theme-light & {
      background-color: transparent;
    }

    &::before {
      font-size: 14px;
      display: flex;
      align-items: center;
      height: 48px;
      padding: 0 12px;
      
      div.theme-dark &, div.theme-light & {
        color: var(--fd-theme-current-blurb);
      }
    }
  }
}


div.contentRegion-3nDuYy {
  z-index: 3;

  div.theme-dark &, div.theme-light & {
    background-color: var(--fd-theme-current-bg);
  }
}
div.contentRegionScroller-26nc1e {
  div.platform-win & {
    padding-top: $titlebar-height;
  }
}

div.contentColumnDefault-1VQkGM {
  padding: 40px;
}

// Close button
div.closeButton-1tv5uR {
  cursor: default;
  height: 48px;
  width: 48px;
  border: 0;
  border-radius: 0;
  position: fixed;
  top: 0;
  left: (260px + 8px) - 48px;

  div.platform-win & {
    top: $titlebar-height;
    margin-left: 1px;
  }

  &:hover {
    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-hover);
    }
  }
  &:active {
    transform: none;

    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-active);
    }
  }

  svg {
    display: none;
  }

  &::after {
    @include fluent-icon('delete_white');
    content: '';
    display: block;
    height: 100%;
    width: 100%;

    div.theme-light & {
      filter: brightness(0);
    }
  }
}
div.keybind-KpFkfr {
  display: none;
}
