*::-webkit-scrollbar {
  width: 8px !important;

  &-track, &-track-piece {
    background-color: transparent !important;
    border: 0 !important;
    border-radius: 0 !important;
  }

  &-thumb {
    border: 2px solid transparent !important;
    border-radius: 0 !important;
    visibility: visible !important;

    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-scrollbar) !important;
    }

    &:hover {
      div.theme-dark &, div.theme-light & {
        background-color: var(--fd-theme-current-scrollbar-hover) !important;
      }
    }
    &:active {
      div.theme-dark &, div.theme-light & {
        background-color: var(--fd-theme-current-scrollbar-active) !important
      }
    }
  }
}
