$public-bg-image: url('https://intrnl.github.io/fluentdiscord/src/assets/background.jpg') !default;
$public-bg-opacity: 0.5 !default;
$public-accent-color: #0078d7 !default;
$public-titlebar-border: #5c5a58 !default;
$public-titlebar-invert: false !default;

:root {
  --int-bg-image: #{$public-bg-image};
  --int-bg-opacity: #{$public-bg-opacity};
  --int-accent-color: #{$public-accent-color};
  --int-titlebar-border: #{$public-titlebar-border};
  
  @if ($public-titlebar-invert) {
    --int-titlebar-invert: 1;
  } @else {
    --int-titlebar-invert: 0;
  }
}
