// Search
div.search-l1Wz-Q {
  margin: 0;
}

div.search-2oPWTC {
  &:not(.open-6_Y_aH):not(.focused-31_ccS) div.searchBar-3dMhjb {
    background-color: transparent;
    width: 48px;

    &::before {
      @include fluent-icon('search_white');
      pointer-events: none;
      content: '';
      height: 100%;
      width: 100%;
      position: absolute;

      div.theme-light & {
        filter: brightness(0);
      }
    }

    div.searchBarIcon-1J6sKG {
      display: none;
    }
    div.DraftEditor-root {
      font-size: 0;
      height: 100%;
      margin: 0;
      padding: 0;
      border: 0;
    }
    div.public-DraftEditor-content {
      cursor: default;

      &:hover {
        div.theme-dark &, div.theme-light & {
          background-color: var(--fd-theme-current-hover);
        }
      }
      &:active {
        div.theme-dark &, div.theme-light & {
          background-color: var(--fd-theme-current-active);
        }
      }
    }
  }

  &.open-6_Y_aH, &.focused-31_ccS {
    background-color: var(--fd-theme-current);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1), 0 1px 8px rgba(0, 0, 0, 0.2);
    border-left: 1px solid var(--fd-theme-current-popout-outline);
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;

    div.platform-win & {
      height: calc(100vh - #{$titlebar-height} - 1px);
      padding-top: $titlebar-height;
      top: -($titlebar-height);
    }
    
    div.searchBar-3dMhjb {
      width: 417px;
    }
  }
}

div.search-2oPWTC div.searchBar-3dMhjb {
  align-items: center;
  transition: none;
  height: 48px;
  border-radius: 0;

  div.theme-dark &, div.theme-light & {
    background-color: var(--fd-theme-current);
  }

  div.DraftEditor-root {
    font-size: 14px;
    font-weight: normal;
    line-height: 22px;
    height: 32px;
    margin: 0 12px;
    border: 2px solid var(--fd-theme-current-outline);
    box-sizing: border-box;

    div.platform-linux & {
      letter-spacing: -0.4px;
    }

    div.theme-dark &, div.theme-light & {
      color: var(--fd-theme-current-invert);
    }

    &:hover {
      div.theme-dark &, div.theme-light & {
        border-color: var(--fd-theme-current-outline-alt);
      }
    }
    &:focus-within {
      div.theme-dark &, div.theme-light & {
        background-color: var(--fd-theme-light-bg);
        color: var(--fd-theme-dark-bg);
        border-color: var(--int-accent-color);
      }
    }

    div.public-DraftEditor-content, div.public-DraftEditorPlaceholder-root {
      padding: 0 10px;
    }
    div.public-DraftEditor-content {
      &::-webkit-scrollbar {
        display: none;
      }
    }
    div.public-DraftEditorPlaceholder-root {
      line-height: 32px - 2px * 2;

      div.theme-dark &, div.theme-light & {
        color: var(--fd-theme-current-blurb);
      }
    }
    div.DraftEditor-editorContainer {
      height: 100%;
    }
    div.public-DraftEditor-content {
      display: flex;
      align-items: center;
    }
  }
  div.searchBarIcon-1J6sKG {
    height: 48px;
    width: 48px;
    margin: 0;
    position: relative;
    top: unset;

    &:hover {
      background-color: var(--fd-theme-current-hover);
    }
    &:active {
      background-color: var(--fd-theme-current-active);
    }

    &::after {
      @include fluent-icon('delete_white');
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;

      div.theme-light & {
        filter: brightness(0);
      }
    }

    i.icon-3cZ1F_ {
      &, &.visible-3V0mGj {
        background-color: transparent;
        background-image: none;
        opacity: 1;
        display: block;
        height: 100%;
        width: 100%;
        position: unset;
      }
    }
  }
}
