@import 'fonts/segoeui';
@import 'fonts/consolas';
@import 'fonts/meiryo';
// @import 'fonts/meiryoui';
// @import 'fonts/mspgothic';

html {
  body, button, input, select, textarea {
    font-family: 'Segoe UI', 'Segoe UI Symbol', 'Segoe UI Emoji', 'Segoe MDL2 Assets', 'Meiryo', 'Malgun Gothic';
  }
  
  *::placeholder {
    font-family: 'Segoe UI', 'Segoe UI Symbol', 'Segoe UI Emoji', 'Segoe MDL2 Assets', 'Meiryo', 'Malgun Gothic';
  }
}
html:lang(ja) {
  body, button, input, select, textarea {
    font-family: 'Segoe UI', 'Meiryo', 'Segoe UI Symbol', 'Segoe UI Emoji', 'Segoe MDL2 Assets', 'Malgun Gothic';
  }
  
  *::placeholder {
    font-family: 'Segoe UI', 'Meiryo', 'Segoe UI Symbol', 'Segoe UI Emoji', 'Segoe MDL2 Assets', 'Malgun Gothic';
  }
}
html:lang(ko) {
  body, button, input, select, textarea {
    font-family: 'Segoe UI', 'Malgun Gothic', 'Segoe UI Symbol', 'Segoe UI Emoji', 'Segoe MDL2 Assets', 'Meiryo';
  }
  
  *::placeholder {
    font-family: 'Segoe UI', 'Malgun Gothic', 'Segoe UI Symbol', 'Segoe UI Emoji', 'Segoe MDL2 Assets', 'Meiryo';
  }
}

code {
  font-family: 'Consolas'
}