div.container-1UB9sr {
  background-color: transparent;
  height: 48px;
  padding: 0 12px;
  border-bottom: 0;

  div.horizontal-1ae9ci {
    display: none;
  }
  &:hover div.horizontal-1ae9ci {
    display: flex;
  }
}

div.inner-btChU7 {
  display: flex;
  flex-direction: row;
  padding-right: 12px;

  a.channel-SEolQq {
    opacity: 1;
    transition: none;
    font-size: 14px;
    line-height: normal;
    margin: 0;

    div.theme-dark &, div.theme-light & {
      color: var(--fd-theme-current-invert);
    }
  }

  div.rtc-connection-status {
    flex-shrink: 0;

    div.theme-dark &, div.theme-light & {
      color: var(--fd-theme-current-invert);
    }

    span {
      line-height: normal;
      margin-right: 8px;
    }
    span.ping {
      @include fluent-icon('signal-none_white');
      background-size: 15px;
      display: block;
      height: 16px;
      width: 16px;
      margin-left: 0;

      div.theme-light & {
        filter: brightness(0);
      }
    }

    & ~ a.channel-SEolQq {
      display: none;
    }
  }
  div.rtc-connection-status-connected {
    span:nth-of-type(2) {
      display: none;
    }

    & ~ a.channel-SEolQq {
      display: inline;
    }
  }

  div.rtc-connection-quality-fine {
    span.ping {
      @include fluent-icon-bg('signal-fine_white');
    }
  }
  div.rtc-connection-quality-average {
    span.ping {
      @include fluent-icon-bg('signal-average_white');
    }
  }
  div.rtc-connection-quality-bad {
    span.ping {
      @include fluent-icon-bg('signal-bad_white');
    }
  }
}

div.button-2KyEfG {
  background-size: 0;
  opacity: 1;
  cursor: default;
  height: 32px;
  width: 32px;
  border-radius: 0;

  &:hover {
    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-dark-hover);
    }
  }
  &:active {
    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-dark-active);
    }
  }

  &::before {
    background-size: 19px;
    background-repeat: no-repeat;
    background-position: center;
    content: '';
    height: 100%;
    width: 100%;

    div.theme-light & {
      filter: brightness(0);
    }
  }

  &.buttonInfo-3nkbOt::before {
    @include fluent-icon-bg('info-circle_white');
  }
  &.buttonDisconnect-3W_SJc::before {
    @include fluent-icon-bg('call-end_white');
  }
}