span.pulsingEllipsisItem-32hhWL {
  animation: fd-pulsing 1.5s infinite ease;
  opacity: 0.25;
  height: 6px;
  width: 6px;
  display: inline-block;
  margin-right: 2px;
  border-radius: 50%;

  @for $i from 1 through 3 {
    &:nth-of-type(#{$i}) {
      animation-delay: ($i - 1) * 0.25s;
    }
  }

  div.theme-dark &, div.theme-light & {
    background-color: var(--fd-theme-current-invert) !important;
  }

  div.stopAnimation-1XE1FK &, div.stop-animation & {
    animation: none;
  }
}
span.typingSpinnerItem-35chrT {
  height: 4px;
  width: 4px;

  div.theme-dark &, div.theme-light & {
    background-color: var(--fd-theme-light-bg) !important;
  }
}
