body {
  background-color: transparent;
}

div.appMount-3lHmkl {
  background-color: transparent;

  &::before {
    background-image: var(--int-bg-image);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    filter: brightness(var(--int-bg-opacity));
    content: '';
    height: 100vh;
    width: 100vw;
    position: absolute;
    z-index: 0;
  }
}

div.app-19_DXt {
  border: 0px solid var(--int-titlebar-border);

  &.platform-win {
    border-bottom-width: 1px;
    border-left-width: 1px;
    border-right-width: 1px;
  }
}

div.layer-3QrUeG, div.layers-3iHuyZ {
  overflow: visible;
  
  div.theme-dark &, div.theme-light & {
    background-color: transparent;
  }
}
div.layer-3QrUeG {
  transform: none !important;

  &:nth-of-type(1) {
    opacity: 1 !important;
    z-index: 3;
  }
  &:nth-of-type(2) {
    z-index: 4;
  }
  

  div.platform-win & {
    padding-top: $titlebar-height;
    top: -($titlebar-height);
  }
}

div.container-2lgZY8 {
  background-color: transparent;
}

div.base-3dtUhz {
  width: 0;
  border-radius: 0;
  position: relative;
  overflow: visible;
  z-index: 2;
}
