div.contextMenu-HLZMGh {
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1), 0 1px 8px rgba(0, 0, 0, 0.2);
  animation: fd-fade-in 0.25s ease;
  width: 205px;
  border: 1px solid transparent;
  border-radius: 0;
  z-index: 4002;
  -webkit-app-region: no-drag;

  &.theme-dark, &.theme-light {
    background-color: var(--fd-theme-current-popout);
    border-color: var(--fd-theme-current-popout-outline);
  }

  & & {
    margin-top: 1px;
    margin-left: 205px;
  }
  &.invertChildX-2fq7sY & {
    margin-left: -205px - 2px;
  }
}

div.itemGroup-1tL0uz {
  border: 0 !important;

  &::before {
    content: '';
    display: block;
    height: 0;
    width: calc(100% - 8px * 2);
    margin: 0 8px;
    border-top: 0px solid transparent;

    div.theme-dark &, div.theme-light & {
      border-top-color: var(--fd-theme-current-popout-outline);
    }
  }

  &:not(:first-child):not(:empty)::before,
  &:not(:empty) ~ &:empty + &:not(:empty)::before {
    border-top-width: 1px;
  }
  &:empty + &:not(:empty)::before {
    border-top-width: 0px;
  }
}

div.item-1Yvehc {
  font-size: 15px;
  font-weight: normal;
  line-height: 20px; // Really hate to do this, but this is for the idiots.
  margin: 0;
  padding: 8px 12px;
  border-radius: 0;
  box-sizing: border-box;

  div.platform-linux &,
  div.platform-linux ~ div & {
    letter-spacing: -0.4px;
  }

  div.theme-dark &, div.theme-light & {
    color: var(--fd-theme-current-invert);
  }

  &:hover {
    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-hover);
      color: var(--fd-theme-current-invert);
    }
  }
  &:active {
    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-active);
      color: var(--fd-theme-current-invert);
    }
  }

  &.danger-2dXSTE {
    &, &:hover {
      color: #f04747 !important;
    }
  }
  &.disabled-2xniQf {
    div.theme-dark &, div.theme-light & {
      color: var(--fd-theme-current-blurb);
    }
  }

  &.itemSubMenu-1vN_Yn {
    padding-right: 12px + 18px;

    div.theme-dark &, div.theme-light & {
      background-image: none;
    }

    &::after {
      @include fluent-icon('arrow-right_white');
      content: '';
      height: 36px;
      width: 18px;
      margin-top: -8px;
      margin-left: auto;
      box-sizing: content-box;
      position: absolute;
      right: 12px;

      div.theme-light & {
        filter: brightness(0);
      }
    }
    
    &:active {
      div.theme-dark &, div.theme-light & {
        background-color: var(--fd-theme-current-hover);
      }
    }
  }
  &.itemToggle-S7XGOQ {
    div.checkbox-3kaeSU div.checkboxInner-3yjcPe {
      span {
        background-size: 18px;
        background-position: center;
        background-repeat: no-repeat;
        border: 0;
        border-radius: 0;

        div.theme-light & {
          filter: brightness(0);
        }

        &::after {
          display: none;
        }
      }
      input.checkboxElement-1qV33p:checked + span {
        @include fluent-icon-bg('checkmark_white');
        background-color: transparent;
      }
    }
  }
  &.itemImage-htIz_v {
    cursor: default !important;
  }
}
