div.tooltip-1OS-Ti {
  background-color: var(--fd-theme-dark) !important;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1), 0 1px 8px rgba(0, 0, 0, 0.2);
  opacity: 0;
  animation: fd-fade-in 0.25s ease;
  animation-delay: 0.35s;
  animation-fill-mode: forwards;
  padding: 8px 10px;
  border: 1px solid var(--fd-theme-dark-popout-outline);
  border-radius: 0;

  &::after {
    display: none;
  }

  div.platform-linux ~ div & {
    letter-spacing: -0.4px;
  }
}
