@mixin fluent-asset ($asset) {
  background-image: url('https://intrnl.github.io/fluentdiscord/src/assets/#{$asset}');
}

@mixin fluent-icon-bg ($icon) {
  @include fluent-asset('icons/#{$icon}.svg');
}

@mixin fluent-icon ($icon) {
  @include fluent-icon-bg($icon);
  background-size: 18px;
  background-position: center;
  background-repeat: no-repeat;
}
