$theme-dark: #141414;
$theme-dark-hover: rgba(#fff, 0.1);
$theme-dark-active: rgba(#fff, 0.2);
$theme-dark-bg: #000000;
$theme-dark-fluent: rgba(#000, 0.25);
$theme-dark-outline: #767676;
$theme-dark-outline-alt: lighten($theme-dark-outline, 20%);
$theme-dark-blurb: #8e8e8e;
$theme-dark-button: rgba(#fff, 0.1);
$theme-dark-button-active: rgba(#fff, 0.3);
$theme-dark-popout: lighten($theme-dark, 7.5%);
$theme-dark-popout-outline: lighten($theme-dark, 20%);
$theme-dark-scrollbar: rgba(#fff, 0.15);
$theme-dark-scrollbar-hover: rgba(#fff, 0.25);
$theme-dark-scrollbar-active: rgba(#fff, 0.45);

$theme-light: #e6e6e6;
$theme-light-hover: rgba(#000, 0.1);
$theme-light-active: rgba(#000, 0.2);
$theme-light-bg: #ffffff;
$theme-light-fluent: rgba(#fff, 0.5);
$theme-light-outline: #8a8a8a;
$theme-light-outline-alt: darken($theme-dark-outline, 20%);
$theme-light-blurb: darken(#7b7b7b, 20%);
$theme-light-button: rgba(#000, 0.1);
$theme-light-button-active: rgba(#000, 0.3);
$theme-light-popout: darken($theme-light, 7.5%);
$theme-light-popout-outline: darken($theme-light, 20%);
$theme-light-scrollbar: rgba(#000, 0.15);
$theme-light-scrollbar-hover: rgba(#000, 0.25);
$theme-light-scrollbar-active: rgba(#000, 0.45);

body {
  --fd-theme-dark: #{$theme-dark};
  --fd-theme-dark-hover: #{$theme-dark-hover};
  --fd-theme-dark-active: #{$theme-dark-active};
  --fd-theme-dark-bg: #{$theme-dark-bg};
  --fd-theme-dark-fluent: #{$theme-dark-fluent};
  --fd-theme-dark-outline: #{$theme-dark-outline};
  --fd-theme-dark-outline-alt: #{$theme-dark-outline-alt};
  --fd-theme-dark-blurb: #{$theme-dark-blurb};
  --fd-theme-dark-button: #{$theme-dark-button};
  --fd-theme-dark-button-active: #{$theme-dark-button-active};
  --fd-theme-dark-popout: #{$theme-dark-popout};
  --fd-theme-dark-popout-outline: #{$theme-dark-popout-outline};
  --fd-theme-dark-scrollbar: #{$theme-dark-scrollbar};
  --fd-theme-dark-scrollbar-hover: #{$theme-dark-scrollbar-hover};
  --fd-theme-dark-scrollbar-active: #{$theme-dark-scrollbar-active};
  --fd-theme-dark-chat-bubble: var(--int-chat-bubble-color, #1e2224);

  --fd-theme-light: #{$theme-light};
  --fd-theme-light-hover: #{$theme-light-hover};
  --fd-theme-light-active: #{$theme-light-active};
  --fd-theme-light-bg: #{$theme-light-bg};
  --fd-theme-light-fluent: #{$theme-light-fluent};
  --fd-theme-light-outline: #{$theme-light-outline};
  --fd-theme-light-outline-alt: #{$theme-light-outline-alt};
  --fd-theme-light-blurb: #{$theme-light-blurb};
  --fd-theme-light-button: #{$theme-light-button};
  --fd-theme-light-button-active: #{$theme-light-button-active};
  --fd-theme-light-popout: #{$theme-light-popout};
  --fd-theme-light-popout-outline: #{$theme-light-popout-outline};
  --fd-theme-light-scrollbar: #{$theme-light-scrollbar};
  --fd-theme-light-scrollbar-hover: #{$theme-light-scrollbar-hover};
  --fd-theme-light-scrollbar-active: #{$theme-light-scrollbar-active};
  --fd-theme-light-chat-bubble: var(--int-chat-bubble-color, #e1dddb);
}
.theme-dark {
  --fd-theme-current: var(--fd-theme-dark);
  --fd-theme-current-invert: var(--fd-theme-light);
  --fd-theme-current-hover: var(--fd-theme-dark-hover);
  --fd-theme-current-active: var(--fd-theme-dark-active);
  --fd-theme-current-bg: var(--fd-theme-dark-bg);
  --fd-theme-current-fluent: var(--fd-theme-dark-fluent);
  --fd-theme-current-outline: var(--fd-theme-dark-outline);
  --fd-theme-current-outline-alt: var(--fd-theme-dark-outline-alt);
  --fd-theme-current-blurb: var(--fd-theme-dark-blurb);
  --fd-theme-current-button: var(--fd-theme-dark-button);
  --fd-theme-current-button-active: var(--fd-theme-dark-button-active);
  --fd-theme-current-popout: var(--fd-theme-dark-popout);
  --fd-theme-current-popout-outline: var(--fd-theme-dark-popout-outline);
  --fd-theme-current-scrollbar: var(--fd-theme-dark-scrollbar);
  --fd-theme-current-scrollbar-hover: var(--fd-theme-dark-scrollbar-hover);
  --fd-theme-current-scrollbar-active: var(--fd-theme-dark-scrollbar-active);
  --fd-theme-current-chat-bubble: var(--fd-theme-dark-chat-bubble);
}
.theme-light {
  --fd-theme-current: var(--fd-theme-light);
  --fd-theme-current-invert: var(--fd-theme-dark);
  --fd-theme-current-hover: var(--fd-theme-light-hover);
  --fd-theme-current-active: var(--fd-theme-light-active);
  --fd-theme-current-bg: var(--fd-theme-light-bg);
  --fd-theme-current-fluent: var(--fd-theme-light-fluent);
  --fd-theme-current-outline: var(--fd-theme-light-outline);
  --fd-theme-current-outline-alt: var(--fd-theme-light-outline-alt);
  --fd-theme-current-blurb: var(--fd-theme-light-blurb);
  --fd-theme-current-button: var(--fd-theme-light-button);
  --fd-theme-current-button-active: var(--fd-theme-light-button-active);
  --fd-theme-current-popout: var(--fd-theme-light-popout);
  --fd-theme-current-popout-outline: var(--fd-theme-light-popout-outline);
  --fd-theme-current-scrollbar: var(--fd-theme-light-scrollbar);
  --fd-theme-current-scrollbar-hover: var(--fd-theme-light-scrollbar-hover);
  --fd-theme-current-scrollbar-active: var(--fd-theme-light-scrollbar-active);
  --fd-theme-current-chat-bubble: var(--fd-theme-light-chat-bubble);
}
