div.titleBar-AC4pGV.typeWindows-1za-n7 {
  -webkit-app-region: drag;
  display: flex;
  flex-direction: row-reverse;
  height: $titlebar-height;
  margin-top: 0;
  border: 1px solid var(--int-titlebar-border);
  border-bottom-color: transparent;
  box-sizing: border-box;
  position: relative;
  z-index: 4001;

  &::before {
    -webkit-app-region: no-drag;
    content: '';
    display: block;
    height: 6px;
    position: absolute;
    top: -1px;
    left: 0;
    right: 0;
  }
}

div.wordmark-2iDDfm {
  opacity: 1;
  display: flex;
  align-items: center;
  padding: 0 14px;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;

  &::before {
    color: #fff;
    filter: #{'invert(var(--int-titlebar-invert))'};
    content: 'Discord';
    font-size: 12px;
    display: block;
  }

  svg {
    display: none;
  }
}

