div.wrapper-1Rf91z {
  width: $guilds-width;
  border-radius: 0;
  position: relative;
  overflow: visible;
  z-index: 1;

  div.theme-dark &, div.theme-light & {
    background-color: transparent;
  }

  &::before {
    background-image: var(--int-bg-image);
    background-color: var(--fd-theme-current-bg);
    background-attachment: fixed;
    transform: scale(1.1);
    filter: brightness(var(--int-bg-opacity)) blur(10px) saturate(150%);
    pointer-events: none;
    content: '';
    width: 100vw;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: -1;

    div.platform-win & {
      height: auto;
      top: -($titlebar-height);
    }
  }
  &::after {
    @include fluent-asset('noise.png');
    mix-blend-mode: exclusion;
    pointer-events: none;
    content: '';
    height: 100%;
    width: calc(100% + 240px);
    position: absolute;
    z-index: -1;

    div.platform-win & {
      height: auto;
      top: -($titlebar-height);
      bottom: 0;
    }
  }
}

