// Guild / DM icon
div.guildIcon-VlWW86, div.avatar-3f3jk4 a.inner-1x-wIi {
  background-size: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: $guilds-height;
  width: $guilds-width;
  border-radius: 0;
  box-sizing: border-box;

  &::before {
    content: '';
    height: 100%;
    width: 100%;
    position: absolute;
  }
  &:hover::before {
    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-hover);
    }
  }
  &:active::before {
    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-active);
    }
  }

  &::after {
    background-image: inherit;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    content: '';
    display: block;
    height: 24px;
    width: 24px;
    border-radius: 50%;
    overflow: hidden;
    z-index: 3;
  }
}
div.noIcon-1a_FrS {
  font-size: 16px * 0.8;
  line-height: normal;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;

  @for $i from 10 through 20 {
    &[style='font-size: #{$i}px;'] {
      font-size: $i * 0.8px !important;
    }
  }

  div.theme-dark &, div.theme-light & {
    color: var(--fd-theme-current-invert);
  }

  &::after {
    display: none;
  }
}

