// Account bar
div.container-2Thooq:not(.powercord-spotify) {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: auto;
  width: auto;
  margin: 0;
  padding: 8px;
  position: absolute;
  bottom: $guilds-separator;
  left: 0;
  z-index: 5;

  div.theme-dark &, div.theme-light & {
    background-color: transparent;
  }

  div.platform-win & {
    margin: -1px;
  }

  // Hide
  background-color: transparent;
  box-shadow: 0 10px 30px transparent, 0 1px 8px transparent;
  border: 1px solid transparent;
  transition: background-color 0.25s ease, box-shadow 0.25s ease, border-color 0.25s ease;
  pointer-events: none;

  &:hover {
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1), 0 1px 8px rgba(0, 0, 0, 0.2);
    transition-delay: 0.35s;
    pointer-events: all;

    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-popout);
      border-color: var(--fd-theme-current-popout-outline);
    }
  }

  & > *:not(.avatarSmall-3ACRaI) {
    transition: opacity 0.25s ease;
    transition-delay: 0;
    opacity: 0;
  }
  
  &:hover > *:not(.avatarSmall-3ACRaI) {
    transition-delay: 0.35s;
    opacity: 1;
  }

  // Avatar
  div.avatarSmall-3ACRaI {
    opacity: 1 !important;
    cursor: default;
    pointer-events: all;
    height: $guilds-height;
    width: $guilds-width;
    margin-right: unset;
    border-radius: 0;
    overflow: hidden;
    position: fixed;
    bottom: $guilds-separator;
    left: 0;

    &:hover {
      background-color: var(--fd-theme-current-hover);
    }
    &:active {
      background-color: var(--fd-theme-current-active);
    }

    div.status-2zcSVk {
      pointer-events: none;
      height: 10px;
      width: 10px;
      border-radius: 50%;
      position: absolute;
      bottom: 20%;
      right: 20%;

      div.theme-dark &, div.theme-light & {
        border: 0;
      }
    }

    div.inner-1W0Bkn {
      background-size: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      overflow: visible;
      position: unset;

      &::before {
        content: '';
        height: 100%;
        width: 100%;
