    padding: 0;
    box-sizing: border-box;
  }

  button.button-2b6hmh {
    background-size: 0;
    opacity: 1;
    cursor: default;
    height: 48px;
    width: 48px;
    border-radius: 0;
    position: relative;

    &:hover {
