div.popoutBottom-1YbShG.noArrow-3BYQ0Z[style*='overflow: hidden;'] {
  box-shadow: 0 0;
  pointer-events: none;
  display: flex;
  transform: none !important;
  height: auto !important;
  width: $channels-width - $guilds-separator * 2 !important;
  overflow: visible !important;
  margin-top: 8px;
  left: $guilds-width + $guilds-separator !important;
}

div.menu-Sp6bN1 {
  animation: fd-fade-in 0.25s ease;
  pointer-events: all;
  width: auto;
  margin-left: auto;
  border: 1px solid transparent;
  border-radius: 0;
  box-sizing: border-box;

  div.theme-dark &, div.theme-light & {
    background-color: var(--fd-theme-current-popout);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1), 0 1px 8px rgba(0, 0, 0, 0.2);
    border-color: var(--fd-theme-current-popout-outline);
  }
}

div.item-1GzJrl {
  font-size: 15px;
  line-height: normal;
  cursor: default;
  display: flex;
  align-items: center;
  padding: 8px 12px;

  div.platform-linux ~ div & {
    letter-spacing: -0.4px;
  }

  &, &.invite-271nFU, &.leave-1DRJfn {
    &, &:hover {
      div.theme-dark &, div.theme-light & {
        color: var(--fd-theme-current-invert);
      }
    }
  }

  &:hover {
    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-hover);
    }
  }
  &:active {
    div.theme-dark &, div.theme-light & {
      background-color: var(--fd-theme-current-active);
    }
  }
}

div.icon-2doZ3q {
  background-size: 0;
  opacity: 1;
  height: 18px;
  width: 18px;
  margin-right: 12px;

  &::before {
    background-size: 18px;
    background-position: center;
    background-repeat: no-repeat;
    content: '';
    display: block;
    height: 100%;
    width: 100%;

    div.theme-light & {
      filter: brightness(0)
    }
